.free-number {
  display: flex;
  flex-direction: column;

  padding-top: 20px;
  padding-left: 55px;
  padding-right: 12px;
}

.free-number__number {
  display: block;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 10px;
  padding-right: 5px;

  @include number();

  &:hover,
  &:focus {
    color: $accent-color;
  }

  &:active {
    color: $basic-grey;
  }
}

.free-number__desc {
  margin: 0;
  padding-left: 10px;

  @include footer-desc();
}
