.social {
  display: flex;
  flex-wrap: wrap;

  margin-left: auto;
  margin-bottom: auto;
  margin-right: -7px;
  padding-top: 20px;
}

.social__list {
  display: flex;
  margin: 0;
  padding: 0;

  list-style-type: none;
}

.social__link {
  display: block;

  padding-top: 10px;
  padding-bottom: 10px;
  padding-right: 8px;
  padding-left: 7px;

  &:hover svg,
  &:focus svg {
    fill: $accent-color;
  }

  &:active svg {
    fill: $basic-grey;
  }
}
